import Vue from 'vue'
import VueRouter from 'vue-router'
import layout from '@/layout/index.vue'
import mLayout from "@/layout/mIndex";

Vue.use(VueRouter)

const originalPush = VueRouter.prototype.push
    //push
VueRouter.prototype.push = function push(location) {
    return originalPush.call(this, location).catch(err => err)
}
//vue
//push


const routes = [
  {
    path: '/mobile',
    name: 'mobile',
    component: mLayout,
    redirect: 'phoneIndex',
    children: [
      {
        path: '/phoneIndex',
        name: 'phoneIndex',
        component: () => import("@/views/mobile/PhoneHome"),
        meta: { title: '' }
      },
      {
        path: 'pabout',
        name: 'paboutUs',
        component: () => import("@/views/mobile/AboutUs"),
        meta: { title: '' }
      },
      {
        path: 'pservice',
        name: 'pserviceArea',
        component: () => import("@/views/mobile/ServiceArea"),
        meta: { title: '' }
      },
      {
        path: 'phelp',
        name: 'phelpCenter',
        component: () => import("@/views/mobile/HelpCenter"),
        meta: { title: '' }
      },
      {
        path: 'prule',
        name: 'pappRule',
        component: () => import("@/views/mobile/AppRule"),
        meta: { title: '' }
      }
    ]
  },
  {
    path: '/',
    // name: 'web',
    component: layout,
    children: [
      {
        path: '',
        name: 'web',
        component: () => import("@/views/web/Home"),
        meta: { title: '' }
      },
      {
        path: 'about',
        name: 'aboutUs',
        component: () => import("@/views/web/AboutUs"),
        meta: { title: '' }
      },
      {
        path: 'service',
        name: 'serviceArea',
        component: () => import("@/views/web/ServiceArea"),
        meta: { title: '' }
      },
      {
        path: 'help',
        name: 'helpCenter',
        component: () => import("@/views/web/HelpCenter"),
        meta: { title: '' }
      },
      {
        path: 'rule',
        name: 'appRule',
        component: () => import("@/views/web/AppRule"),
        meta: { title: '' }
      }
    ]
  }
]

const router = new VueRouter({
  routes
})

export default router
